import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { RootState } from '@/store/index'
import { 
  TrendingUp, 
  Award,
  CheckCircle
} from 'lucide-react'

export const DashboardPage: React.FC = () => {
  const [loading, setLoading] = useState(true)
  const { user } = useSelector((state: RootState) => state.auth)

  useEffect(() => {
    setLoading(false)
  }, [])

  const formatTimestamp = (timestamp: string) => {
    return new Date(timestamp).toLocaleString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }


  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Welcome Header */}
        <div className="mb-8">
          <div className="bg-gradient-to-r from-blue-600 to-purple-600 rounded-lg p-6 text-white">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-2xl font-bold mb-2">
                  Welcome back, {user?.firstName || 'Principal'}!
                </h1>
                <p className="text-blue-100">
                  {user?.schoolName ? `Leading at ${user.schoolName}` : 'Your gateway to educational excellence'}
                </p>
              </div>
              <div className="flex items-center space-x-4">
                <div className="text-right">
                  <p className="text-sm text-blue-100">Member since</p>
                  <p className="text-lg font-semibold">{formatTimestamp(new Date().toISOString())}</p>
                </div>
              </div>
            </div>
            <div className="mt-4 flex items-center space-x-6">
              <div className="flex items-center">
                <CheckCircle className="w-5 h-5 text-green-300 mr-2" />
                <span className="text-sm">Profile Complete</span>
              </div>
              <div className="flex items-center">
                <Award className="w-5 h-5 text-yellow-300 mr-2" />
                <span className="text-sm">3 Achievements</span>
              </div>
              <div className="flex items-center">
                <TrendingUp className="w-5 h-5 text-blue-300 mr-2" />
                <span className="text-sm">Top Contributor</span>
              </div>
            </div>
          </div>
        </div>
      
      {/* Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          <div className="bg-white p-6 rounded-lg shadow">
            <h3 className="text-lg font-medium text-gray-900">PrinciPosts</h3>
            <p className="mt-2 text-sm text-gray-600">Share your insights and experiences</p>
          </div>
          
          <div className="bg-white p-6 rounded-lg shadow">
            <h3 className="text-lg font-medium text-gray-900">PrinciVoice</h3>
            <p className="mt-2 text-sm text-gray-600">Video messages for the community</p>
          </div>
          
          <div className="bg-white p-6 rounded-lg shadow">
            <h3 className="text-lg font-medium text-gray-900">PrinciCare</h3>
            <p className="mt-2 text-sm text-gray-600">Wellness and mental health support</p>
          </div>
        </div>
      </div>
    </div>
  )
}
